import { Service } from '@usercentrics/cmp-browser-sdk';
import { useState } from 'preact/hooks';

interface SubServiceHooks {
  selectedSubService: Service | null;
  onOpenSubServiceDetail: (subService: Service) => void;
  onCloseSubServiceDetail: () => void;
}

export const useSubService = (): SubServiceHooks => {
  const [selectedSubService, setSelectedSubService] = useState<Service | null>(null);

  const onOpenSubServiceDetail = (subService: Service) => {
    setSelectedSubService(subService);
  };

  const onCloseSubServiceDetail = () => {
    if (selectedSubService) {
      setSelectedSubService(null);
    }
  };

  return {
    onCloseSubServiceDetail,
    onOpenSubServiceDetail,
    selectedSubService,
  };
};
