import { CSS_SELECTOR, useCustomCss } from '../../contexts/CustomCss';
import { CheckMarkIcon, GlobeIcon } from '../../assets';
import { GlobeContainer, Menu, MenuContainer, MenuItem, TextContainer } from './Selector.styles';
import { createRef, h } from 'preact';
import { useCallback, useEffect, useState } from 'preact/hooks';
import { useKeyPress, usePrevious } from '../../hooks';

interface Props<T> {
  onChange: (selected: T) => void;
  items: SelectItem<T>[];
  ariaLabel?: string;
  defaultItem?: SelectItem<T>;
  id?: string;
  isExpandedOnInit?: boolean;
  maxHeight?: number;
  testIdContainer?: string;
  testIdMenu?: string;
  testIdMenuButton?: string;
}

type SelectItem<T> = {
  ariaLabel: string;
  displayName: string;
  value: T;
};

function Selector<T>({
  onChange,
  items,
  ariaLabel,
  defaultItem,
  id,
  isExpandedOnInit,
  maxHeight,
  testIdContainer,
  testIdMenu,
  testIdMenuButton,
}: Props<T>): h.JSX.Element {
  const [dropdownExpanded, setDropdownExpanded] = useState(isExpandedOnInit);
  const prevDropdownExpanded = usePrevious(dropdownExpanded);
  const [selectedItem, setSelected] = useState(defaultItem || items[0]);
  const isUpArrowKeyPressed = useKeyPress({ targetKey: 'ArrowUp' });
  const isDownArrowKeyPressed = useKeyPress({ targetKey: 'ArrowDown' });
  const isEscapePressed = useKeyPress({ targetKey: 'Escape' });

  const { rules } = useCustomCss(CSS_SELECTOR.LANGUAGE_SWITCH);
  const selectedMenuItem = createRef();
  const globeIcon = createRef();

  const clickListener = useCallback(
    (event: MouseEvent) => {
      // eslint-disable-next-line
      const clickPath = ((event as any).path as EventTarget[]) || (event.composedPath && event.composedPath());

      if (clickPath) {
        // eslint-disable-next-line
        const isPartOfLanguageSelector = clickPath.some((elem) => (elem as any).id === id);

        if (!isPartOfLanguageSelector) {
          setDropdownExpanded(false);
        }
      }
    },
    [id],
  );

  useEffect(() => {
    if (isEscapePressed && dropdownExpanded) {
      setDropdownExpanded(!dropdownExpanded);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isEscapePressed]);

  useEffect(() => {
    if (dropdownExpanded && isDownArrowKeyPressed) {
      if (!document.activeElement) {
        selectedMenuItem.current.focus();
      } else {
        const activeElement = document.activeElement?.shadowRoot?.querySelector(':focus-visible');

        if (activeElement?.getAttribute('role') === 'option' && activeElement.nextSibling) {
          (activeElement.nextSibling as HTMLElement).focus();
        }
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isDownArrowKeyPressed]);

  useEffect(() => {
    if (dropdownExpanded && isUpArrowKeyPressed) {
      if (!document.activeElement) {
        selectedMenuItem.current.focus();
      } else {
        const activeElement = document.activeElement?.shadowRoot?.querySelector(':focus-visible');

        if (activeElement?.getAttribute('role') === 'option' && activeElement.previousSibling) {
          (activeElement.previousSibling as HTMLElement).focus();
        }
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isUpArrowKeyPressed]);

  useEffect(() => {
    document.addEventListener('click', clickListener);

    return (): void => {
      document.removeEventListener('click', clickListener);
    };
  }, [clickListener]);

  useEffect(() => {
    if (dropdownExpanded && selectedMenuItem) {
      selectedMenuItem.current.focus();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [dropdownExpanded]);

  useEffect(() => {
    if (!dropdownExpanded && globeIcon && prevDropdownExpanded) {
      globeIcon.current.focus();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [dropdownExpanded, globeIcon]);

  useEffect(() => {
    setSelected(defaultItem || items[0]);
  }, [defaultItem, items]);

  const onClickHandler = useCallback(
    (selectItem: SelectItem<T>): void => {
      setSelected(selectItem);
      onChange(selectItem.value);
    },
    [onChange],
  );

  return (
    <MenuContainer data-testid={testIdContainer} dropdownExpanded={dropdownExpanded} id={id}>
      {dropdownExpanded ? (
        <Menu aria-expanded role="listbox" data-testid={testIdMenu} maxHeight={maxHeight}>
          <MenuItem
            aria-label={selectedItem.ariaLabel}
            aria-posinset={1}
            aria-selected
            aria-setsize={items && items.length}
            data-testid={`uc-menu-item-${selectedItem.value}`}
            onClick={(): void => {
              setDropdownExpanded(!dropdownExpanded);
            }}
            role="option"
            ref={selectedMenuItem}
            style={{ ...rules }}
          >
            <TextContainer>{selectedItem.displayName}</TextContainer>
            <CheckMarkIcon />
          </MenuItem>
          {items
            .filter((item) => item.value !== selectedItem.value)
            .map((item, index) => {
              return (
                <MenuItem
                  aria-label={item.ariaLabel}
                  aria-posinset={index + 2}
                  aria-setsize={items && items.length}
                  data-testid={`uc-menu-item-${item.value}`}
                  onClick={(): void => {
                    setDropdownExpanded(!dropdownExpanded);
                    onClickHandler(item);
                  }}
                  role="option"
                  style={{ ...rules }}
                >
                  {item.displayName}
                </MenuItem>
              );
            })}
        </Menu>
      ) : (
        <GlobeContainer
          aria-haspopup="listbox"
          aria-label={ariaLabel}
          data-testid={testIdMenuButton}
          onClick={(): void => {
            setDropdownExpanded(true);
          }}
          ref={globeIcon}
        >
          <GlobeIcon />
        </GlobeContainer>
      )}
    </MenuContainer>
  );
}

Selector.defaultProps = {
  ariaLabel: null,
  defaultItem: null,
  id: '',
  isExpandedOnInit: false,
  maxHeight: 100,
  testIdContainer: '',
  testIdMenu: '',
  testIdMenuButton: '',
};
export default Selector;
