import { DefaultUISettings, TCFUISettings, UI_VARIANT } from '@usercentrics/cmp-browser-sdk';
import { FunctionalComponent, h } from 'preact';
import { SubServiceInfo, SubServiceInfoLabel, SubServicesInfoArrow } from './SubServices.styles';
import { useSettingsContext } from '../../contexts/SettingsContext';
import { useUiContext } from '../../contexts/UiContext';

type Divider = 'arrow' | 'bullet';

interface Props {
  divider?: Divider;
  size: number;
}

const SubServicesInfo: FunctionalComponent<Props> = ({ divider = 'arrow', size }) => {
  const { uiVariant } = useUiContext();
  const { settings } = useSettingsContext();
  const labels =
    uiVariant === UI_VARIANT.TCF
      ? (settings.ui as TCFUISettings).labels.nonTCFLabels.general
      : (settings.ui as DefaultUISettings).labels.general;

  const subServiceSingOrPlural = size === 1 ? labels.subservice : labels.subservices;

  return (
    <SubServiceInfo showBullet={divider === 'bullet'}>
      {size}
      <SubServiceInfoLabel>{subServiceSingOrPlural}</SubServiceInfoLabel>
      {divider === 'arrow' && <SubServicesInfoArrow color="rgb(48, 48, 48)" direction="right" />}
    </SubServiceInfo>
  );
};

export default SubServicesInfo;
