import { FunctionalComponent, h } from 'preact';
import { TabList as StyledTabList } from './Tabs.styles';
import { TabListProps } from './interface';
import { forwardRef } from 'preact/compat';

export const TabList: FunctionalComponent<TabListProps> = forwardRef<HTMLDivElement, TabListProps>(
  ({ children, ariaLabel }, ref) => {
    return (
      <StyledTabList ref={ref} data-aria-label={ariaLabel} role="tablist">
        {children}
      </StyledTabList>
    );
  },
);
