import { Button, IdentifierText, TextWrapper } from './ControllerIdItem.styles';
import { CSS_SELECTOR, useCustomCss } from '../../contexts/CustomCss';
import { Fragment, FunctionalComponent, h } from 'preact';
import { Section, Title } from '../List';
import { CopyIcon } from '../../assets';
import { VerticalSpacer } from '../Spacers/VerticalSpacer';
import { memo } from 'preact/compat';
import { useSettingsContext } from '../../contexts/SettingsContext';

type props = {
  label: string;
};

const ControllerIdItem: FunctionalComponent<props> = memo(({ label }) => {
  const { settings } = useSettingsContext();
  const { rules } = useCustomCss(CSS_SELECTOR.TAB_CONTENT);

  let color: string | undefined;

  if (rules && rules.color) {
    color = rules.color;
  }

  return (
    <Fragment>
      <VerticalSpacer custom={14} />
      <Section>
        <Title style={color ? { color } : {}}>{label}</Title>
        <TextWrapper>
          <IdentifierText style={color ? { color } : {}}>{settings.controllerId}</IdentifierText>
          <Button
            aria-label={settings.ui.ariaLabels.copyControllerId}
            data-testid="uc-controller-item-button"
            title="Copy"
            onClick={(): void => {
              navigator.clipboard.writeText(settings.controllerId);
            }}
          >
            <CopyIcon fill={color} />
          </Button>
        </TextWrapper>
      </Section>
    </Fragment>
  );
});

export default ControllerIdItem;
