import { Anchor, CenterContainer, SideContainer } from './PoweredBy.styles';
import { Fragment, FunctionalComponent, h } from 'preact';

import { useSettingsContext } from '../../contexts/SettingsContext';

type Props = {
  sidePosition?: boolean;
};

const PoweredBy: FunctionalComponent<Props> = ({ sidePosition = false }) => {
  const {
    settings: { ui },
  } = useSettingsContext();
  const {
    poweredBy: { isEnabled, url, urlLabel, partnerUrl, partnerUrlLabel, label },
  } = ui;

  if (!isEnabled) {
    return <Fragment />;
  }

  const standardPoweredBy = url ? (
    <Anchor href={url} target="_blank" rel="noopener">
      {urlLabel}
    </Anchor>
  ) : (
    urlLabel
  );

  const partnerPoweredBy = partnerUrl ? (
    <Anchor href={partnerUrl} target="_blank" rel="noopener">
      {partnerUrlLabel}
    </Anchor>
  ) : (
    partnerUrlLabel
  );

  const content = (
    <Fragment>
      {label}
      {String.fromCharCode(160)}
      {urlLabel && standardPoweredBy}
      {urlLabel && partnerUrlLabel && (
        <Fragment>
          {String.fromCharCode(160)}
          &amp;
          {String.fromCharCode(160)}
        </Fragment>
      )}
      {partnerUrlLabel && partnerPoweredBy}
    </Fragment>
  );

  return (
    <Fragment>
      {sidePosition ? <SideContainer>{content}</SideContainer> : <CenterContainer>{content}</CenterContainer>}
    </Fragment>
  );
};

export default PoweredBy;
