import { Fragment, FunctionalComponent, h } from 'preact';
import Selector from '../Selector';
import { languageList } from './LanguageList';
import { memo } from 'preact/compat';
import { useSettingsContext } from '../../contexts/SettingsContext';

type Props = {
  onChange: (selected: string) => void;
};

const LanguageSelector: FunctionalComponent<Props> = memo(({ onChange }) => {
  const { settings } = useSettingsContext();
  const mappedLanguage = settings.ui.language.available.sort().map((language) => {
    return {
      ariaLabel: languageList[language.toUpperCase() as keyof typeof languageList]?.language,
      displayName: languageList[language.toUpperCase() as keyof typeof languageList]?.displayName,
      value: language.toUpperCase(),
    };
  });

  if (settings.ui.language.available.length <= 1) {
    return <Fragment />;
  }

  return (
    <Selector<string>
      onChange={onChange}
      items={mappedLanguage}
      ariaLabel={settings.ui.ariaLabels.languageSelector}
      defaultItem={{
        ariaLabel: languageList[settings.ui.language.selected.toUpperCase() as keyof typeof languageList]?.language,
        displayName:
          languageList[settings.ui.language.selected.toUpperCase() as keyof typeof languageList]?.displayName,
        value: settings.ui.language.selected.toUpperCase(),
      }}
      maxHeight={260}
      id="usercentrics-language-selector"
      testIdContainer="uc-language-container"
      testIdMenuButton="uc-language-button"
      testIdMenu="uc-language-menu"
    />
  );
});

export default LanguageSelector;
