type ToggleProps = {
  hasToggle?: boolean;
  hideToggles?: boolean;
};

const resolveDisplay = (hasToggle: boolean | undefined, hideToggles: boolean | undefined) => {
  if (hideToggles) {
    return 'inline-flex';
  }

  if (hasToggle) {
    return 'block';
  }

  return 'flex';
};

export const CardHeader = styled.div<ToggleProps>`
  display: flex;
  flex-direction: row;

  ${({ hasToggle, hideToggles, theme }) =>
    theme.uiVariant !== UI_VARIANT.CCPA &&
    css`
      ${media.mobilePortrait} {
        align-items: center;
        display: ${resolveDisplay(hasToggle, hideToggles)};
      }
    `}
`;

const resolveBorder = (hasToggle: boolean | undefined, hideToggles: boolean | undefined, tertiaryColor: string) => {
  if (hideToggles || !hasToggle) {
    return 'none';
  }

  return `1px solid ${tertiaryColor}`;
};

export const TextContainer = styled.button<ToggleProps>`
  ${({ theme }) => getCssBasedOnRtl(theme, CSS_PROPERTY.TEXT_ALIGNMENT_LEFT)};
  background-color: white;
  border: none;
  display: flex;
  font-size: ${({ theme }): string => theme.typography.size.medium};
  flex: 1 1 40%;
  flex-direction: column;
  ${({ theme }) => getCssBasedOnRtl(theme, CSS_PROPERTY.MARGIN_RIGHT, theme.spacing.base.xs)};
  outline: none;
  padding: ${({ theme }) => theme.spacing.base.sm};
  word-break: break-word;
  vertical-align: middle;

  &:focus-visible {
    outline-style: auto;
    outline-width: 5px;
  }

  ${({ hasToggle, hideToggles, theme }) =>
    theme.uiVariant !== UI_VARIANT.CCPA &&
    css`
      ${media.mobilePortrait} {
        border-bottom: ${resolveBorder(hasToggle, hideToggles, theme.typography.color.tertiary)};
        border-top-left-radius: 4px;
        border-top-right-radius: 4px;
        margin: 0;
        padding: ${theme.spacing.mobile.xl};
        width: 100%;
      }
    `}
`;

