const RowComponent = styled.div<RowProps>`
  align-items: center;
  display: flex;
  flex-direction: row;

  ${media.mobilePortraitXS} {
    ${({ theme }): string =>
      getCssBasedOnRtl(theme, CSS_PROPERTY.BORDER_RIGHT, 1, CSS_BORDER_TYPE.SOLID, theme.typography.color.tertiary)};
    flex: 2;
    flex-wrap: wrap;

    > div {
      margin-top: ${({ theme }) => theme.spacing.mobile.lg};
    }

    div:last-child {
      margin-bottom: ${({ theme }) => theme.spacing.mobile.lg};
    }
  }
`;

type RowProps = {
  children: ComponentChild[];
  reverseDirection?: boolean;
};

export const Row: FunctionalComponent<RowProps> = ({ children, reverseDirection = false }) => {
  return <RowComponent>{reverseDirection ? children.reverse() : children}</RowComponent>;
};
