/* eslint-disable func-names */
/* eslint-disable @typescript-eslint/ban-ts-comment */
/* eslint-disable dot-notation */
/* eslint-disable react/jsx-props-no-spreading */
import { ANALYTICS_TYPE, LAYER_PLACEMENT, VIEW } from '../../enums';
import {
  BannerContainer,
  BannerWrapper,
  CenterContainer,
  ColumnContainer,
  FadingContainer,
  MobileContainer,
  MobileFadingWrapper,
  MobileScrollContent,
  RowContainer,
  SideContainer,
} from './MainLayout.styles';
import { CSS_SELECTOR, useCustomCss } from '../../contexts/CustomCss';
import { Fragment, FunctionalComponent, h } from 'preact';
import { MOBILE_FADING_ID, MOBILE_SCROLL_ID, ROOT_ID } from '../../constants';
import { useEffect, useRef } from 'preact/hooks';
import AnchorList from '../AnchorList';
import ButtonsContainer from '../ButtonsContainer/ButtonsContainer';
import { ButtonsContainerProps } from '../ButtonsContainer/interface';
import CustomizeAnchor from '../CustomizeAnchor/CustomizeAnchor';
import Footer from '../Footer/Footer';
import Header from '../Header';
import { HeaderWrapper } from '../Header/Header.styles';
import Overlay from '../Overlay/Overlay';
import PoweredBy from '../PoweredBy';
import { SECOND_LAYER_SIDE } from '@usercentrics/cmp-browser-sdk';
import ShowMore from '../ShowMore/ShowMore';
import VerticalScroller from '../VerticalScroller/VerticalScroller';
import { VerticalSpacer } from '../Spacers/VerticalSpacer';
import { memo } from 'preact/compat';
import { useDeviceState } from '../../hooks';
import { useSettingsContext } from '../../contexts/SettingsContext';
import { useUiContext } from '../../contexts/UiContext';

export type LayerSettings = {
  isOverlayEnabled: boolean;
  layerPlacement: LAYER_PLACEMENT | null;
  minVerticalScrollerHeight?: number;
  placeLinksInFooter?: boolean;
  showBorderBottom?: boolean;
  side?: SECOND_LAYER_SIDE;
  testId: string;
  withFadingBottom?: boolean;
  withFadingTop?: boolean;
  withInitialHeight?: boolean;
};

type Props = {
  customActions?: { ariaLabel: string; label: string; onClick: () => void }[];
  footer: {
    buttons: ButtonsContainerProps;

    showBorderBeforeFooter?: boolean;
  };
  header: {
    description: string;
    labelShowMore?: string;
    maxDescriptionHeight?: number;
    showCloseButton?: boolean;
    shortDescription?: string;
    showFullScreenButton?: boolean;
    showLanguageSelector?: boolean;
    showMoreEnabled?: boolean;
    title: string;
    functions: {
      onClose?: () => void;
      onFullScreenAmp?: () => void;
      onLanguageChange: (selectedLanguage: string) => void;
    };
    enableHTMLTextFormatting: boolean;
  };
  layerSettings: LayerSettings;
  useBackgroundColor: boolean;
};

const MainLayout: FunctionalComponent<Props> = memo(
  ({ children, customActions, footer, header, layerSettings, useBackgroundColor = false }) => {
    const { settings } = useSettingsContext();
    const { isAmpEnabled, view } = useUiContext();
    const { ui } = settings;
    const { isDesktop, isMobile, isPortrait, isLandscape } = useDeviceState();
    const mainElementRef = useRef<HTMLDivElement>(null);
    const { rules } = useCustomCss(CSS_SELECTOR.LAYER);

    // This fixes the issue when there is a smart app banner being displayed on Safari
    useEffect(() => {
      const isSafari = (function (p) {
        return p.toString() === '[object SafariRemoteNotification]';
        // @ts-ignore
      })(!window['safari'] || (typeof window['safari'] !== 'undefined' && window['safari'].pushNotification));

      const documentHeight =
        document.documentElement.clientHeight <= document.body.clientHeight
          ? document.documentElement.clientHeight
          : document.body.clientHeight;

      const updatePositionOfCMPContainer = () => {
        if (isSafari && documentHeight > window.innerHeight && !document.hidden && isMobile) {
          const root = document.getElementById(ROOT_ID);
          const container = root?.shadowRoot?.querySelector<HTMLElement>('#uc-center-container') || null;

          if (container !== null) {
            container.style.bottom = `${documentHeight - window.innerHeight}px`;
          }
        }
      };

      updatePositionOfCMPContainer();
      // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    useEffect(() => {
      if (mainElementRef.current) {
        mainElementRef.current.focus({ preventScroll: true });
      }
    });

    const buttonComponent = <ButtonsContainer {...footer.buttons} />;

    const poweredByComponent = <PoweredBy sidePosition={layerSettings.placeLinksInFooter} />;

    const linkComponent = (
      <AnchorList
        links={[
          { ...ui.links.privacyPolicy, type: ANALYTICS_TYPE.PRIVACY_POLICY_LINK },
          { ...ui.links.cookiePolicy, type: ANALYTICS_TYPE.COOKIE_POLICY_LINK },
          { ...ui.links.imprint, type: ANALYTICS_TYPE.IMPRINT_LINK },
        ]}
        withoutMargin
      >
        <Fragment>
          {customActions &&
            customActions.map((action) => (
              <CustomizeAnchor ariaLabel={action.ariaLabel} label={action.label} onClick={action.onClick} />
            ))}
        </Fragment>
      </AnchorList>
    );

    const position = view === VIEW.FIRST_LAYER ? ui.customization.logo.position : 'left';

    const headerComponent = (
      <HeaderWrapper>
        <Header
          ariaLabel={ui.ariaLabels.usercentricsCMPHeader}
          closeAriaLabel={ui.ariaLabels.closeButton}
          fullScreenAriaLabel={ui.ariaLabels.fullscreenButton}
          onClose={header.functions.onClose}
          logo={{ ...ui.customization.logo, position }}
          onFullScreen={header.functions.onFullScreenAmp}
          onLanguageChange={header.functions.onLanguageChange}
          showCloseButton={header.showCloseButton}
          showFullScreenButton={header.showFullScreenButton}
          showLanguage={header.showLanguageSelector && settings.ui.language.available.length > 1}
          title={header.title}
        >
          <ShowMore
            enableHTMLTextFormatting={header.enableHTMLTextFormatting}
            isShowMoreEnabled={header.showMoreEnabled}
            longDescription={header.description}
            maxDescriptionHeight={header.maxDescriptionHeight}
            shortDescription={header.shortDescription}
            labelShowMore={header.labelShowMore}
            withFading
          />

          {!layerSettings.placeLinksInFooter && (
            <Fragment>
              <VerticalSpacer small />
              {linkComponent}
            </Fragment>
          )}
        </Header>
      </HeaderWrapper>
    );

    const footerComponent = (
      <Footer ariaLabel={ui.ariaLabels.usercentricsCMPButtons} showBorder={footer.showBorderBeforeFooter}>
        {layerSettings.placeLinksInFooter ? (
          <RowContainer>
            <ColumnContainer>
              {linkComponent}
              {(isDesktop || isLandscape) && poweredByComponent}
            </ColumnContainer>

            {buttonComponent}
            {isMobile && isPortrait && poweredByComponent}
          </RowContainer>
        ) : (
          <Fragment>
            {buttonComponent}
            {poweredByComponent}
          </Fragment>
        )}
      </Footer>
    );

    const mainElement = isMobile ? (
      <MobileContainer>
        <MobileFadingWrapper
          id={MOBILE_FADING_ID}
          isMobile={isMobile}
          withFadingBottom={layerSettings.withFadingBottom}
          withFadingTop={layerSettings.withFadingTop}
        >
          <MobileScrollContent
            id={MOBILE_SCROLL_ID}
            data-testid="uc-vertical-scroller"
            useBackgroundColor={useBackgroundColor}
          >
            {headerComponent}
            {children}
          </MobileScrollContent>
        </MobileFadingWrapper>
        {footerComponent}
      </MobileContainer>
    ) : (
      <Fragment>
        {headerComponent}
        {children && (
          <VerticalScroller
            ariaLabel={ui.ariaLabels.usercentricsCMPContent}
            minHeight={layerSettings.minVerticalScrollerHeight}
            useBackgroundColor={useBackgroundColor}
            withFadingBottom={layerSettings.withFadingBottom}
            withFadingTop={layerSettings.withFadingTop}
          >
            <FadingContainer useBackgroundColor={useBackgroundColor}>{children}</FadingContainer>
          </VerticalScroller>
        )}
        {footerComponent}
      </Fragment>
    );

    switch (layerSettings.layerPlacement) {
      case LAYER_PLACEMENT.CENTER:
        return (
          <Fragment>
            {layerSettings.isOverlayEnabled && !isAmpEnabled && <Overlay />}
            <CenterContainer
              tabIndex={0}
              ref={mainElementRef}
              id="uc-center-container"
              data-testid={layerSettings.testId}
              isAmpEnabled={isAmpEnabled}
              showBorderBottom={layerSettings.showBorderBottom || false}
              withInitialHeight={layerSettings.withInitialHeight || false}
              style={{ ...rules }}
            >
              {mainElement}
            </CenterContainer>
          </Fragment>
        );
      case LAYER_PLACEMENT.SIDE:
        return (
          <Fragment>
            {layerSettings.isOverlayEnabled && !isAmpEnabled && <Overlay />}
            <SideContainer
              tabIndex={0}
              ref={mainElementRef}
              id="uc-side-container"
              data-testid={layerSettings.testId}
              privacyButtonPosition={ui.privacyButton.position}
              showBorderBottom={layerSettings.showBorderBottom || false}
              side={layerSettings.side}
              style={{ ...rules }}
            >
              {mainElement}
            </SideContainer>
          </Fragment>
        );

      case LAYER_PLACEMENT.TOP:
      case LAYER_PLACEMENT.BOTTOM:
        return (
          <Fragment>
            {layerSettings.isOverlayEnabled && !isAmpEnabled && <Overlay />}
            <BannerWrapper
              tabIndex={0}
              ref={mainElementRef}
              showBorderBottom={layerSettings.showBorderBottom || false}
              layerPlacement={layerSettings.layerPlacement}
              isAmpEnabled={isAmpEnabled}
            >
              <BannerContainer
                tabIndex={0}
                data-testid={layerSettings.testId}
                showBorderBottom={layerSettings.showBorderBottom || false}
                layerPlacement={layerSettings.layerPlacement}
                isAmpEnabled={isAmpEnabled}
                style={{ ...rules }}
              >
                {mainElement}
              </BannerContainer>
            </BannerWrapper>
          </Fragment>
        );
      default:
        return <Fragment />;
    }
  },
);

export default MainLayout;
